import React, { useLayoutEffect, useRef } from 'react';
import { Input } from '@chakra-ui/react';
import Inputmask from 'inputmask';

type Props = {
    value: string;
    onChange: (v: string, isValid: boolean) => void;
}

const mask = '99-9999999';

const EinInput:React.FC<Props> = ({ onChange, value }) => {
  const input = useRef(null);

  useLayoutEffect(() => {
    if (input.current) {
      Inputmask({
        mask,
        showMaskOnHover: false,
        showMaskOnFocus: false,
        jitMasking: true,
      }).mask(input.current);
    }
  }, []);
  return (
    <Input
      data-testid="ein-input"
      ref={input}
      variant="primary"
      onChange={(e) => {
        const v = e.target.value;
        const isValid = Inputmask.isValid(v, { mask });
        const unmaskedValue: string = (input.current as any)?.inputmask.unmaskedvalue();
        onChange(unmaskedValue, isValid);
      }}
      value={value}
    />
  );
};

EinInput.defaultProps = {
};

export default EinInput;
