import React, {
  useCallback, useEffect, useMemo, useState
} from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box, Button, FormControl, FormLabel, Input, Select, Text
} from '@chakra-ui/react';
import { shallowEqual } from 'react-redux';
import { useNavigate, useSearchParams } from 'react-router-dom';
import EinInput from '../../../../components/EinInput';
import MiniBox from '../../../../layout/MiniBox';
import { useAppAsyncDispatch, useAppSelector } from '../../../../hooks/store';
import {
  createbusiness, updatebusiness
} from '../../../../store/middlewares/business';
import { apiErrorHandler } from '../../../../utils/general';

const entityTypes = [
  { key: 'soleProprietor', label: 'Sole Proprietor' },
  { key: 'singleMemberLLC', label: 'Single Member LLC' },
  { key: 'limitedLiabilityCompany', label: 'Limited Liability Company' },
  { key: 'generalPartnership', label: 'General Partnership' },
  { key: 'unlistedCorporation', label: 'Unlisted Corporation' },
  {
    key: 'publiclyTradedCorporation',
    label: 'Publicly Traded Corporation'
  },
  { key: 'association', label: 'Association' },
  { key: 'nonProfit', label: 'Non Profit' },
  { key: 'governmentOrganization', label: 'Government Organization' },
  { key: 'revocableTrust', label: 'Revocable Trust' },
  { key: 'irrevocableTrust', label: 'Irrevocable Trust' },
  { key: 'estate', label: 'estate' }
];

/**
 * Component for collecting the details of the business.
 *
 * @component
 * @module KYB
 * @link {@see https://www.figma.com/file/ndxr4b9PADQG9FMkIWp2ie/For-Solid-(Product)?node-id=22252%3A122634}
 * @returns {JSX.Element}
 */
const BusinessDetails = () => {
  const { t } = useTranslation();
  const dispatch = useAppAsyncDispatch();
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();

  const businessId = searchParams.get('businessId');

  const person = useAppSelector((store) => store.user.personDetails, shallowEqual);
  const businessDetails = useAppSelector((store) => store.business.selectedBusiness, shallowEqual);

  const [formData, updateData] = useState({
    id: '',
    legalName: '',
    entityType: entityTypes[0].key || '',
    dba: '',
    idNumber: '',
    idType: 'ein'
  });

  useEffect(() => {
    if (businessId && businessDetails) {
      updateData({
        id: businessDetails.id,
        legalName: businessDetails.legalName,
        entityType: businessDetails.entityType,
        dba: businessDetails.dba,
        idNumber: businessDetails.idNumber,
        idType: businessDetails.idType,
      });
    }
  }, [businessId, businessDetails]);

  const isValid = useMemo(() => formData.legalName.length !== 0
    && formData.entityType.length !== 0
    && formData.idNumber.length !== 0, [formData]);

  const submit = useCallback(() => {
    if (person && isValid) {
      const { id, ...otherFormData } = formData;
      if (id.length === 0) {
        // create business
        dispatch(createbusiness({
          ...otherFormData,
          email: person.email
        })).then(() => {
          navigate('/ao/kyb/businessAddress');
        }).catch(apiErrorHandler);
      } else {
        // update business
        dispatch(updatebusiness(id, {
          ...otherFormData,
        })).then(() => {
          navigate('/ao/kyb/businessAddress');
        }).catch(apiErrorHandler);
      }
    }
  }, [person, isValid, formData]);

  return (
    <div className="page">
      <form
        autoComplete="off"
        onSubmit={(e) => {
          e.preventDefault();
          submit();
        }}
      >
        <MiniBox
          varient="box"
          heading={t('business_details.title')}
          showBack
          footer={(
            <Button data-testid="submit-btn" disabled={!isValid} width="100%" variant="primary" type="submit">
              {t('business_details.ctaText')}
            </Button>
          )}
          onBack={() => {
            navigate(-1);
          }}
        >
          <Box width="100%">
            <FormControl variant="primary">
              <FormLabel variant="primary">
                {t('business_details.legalBusinessName')}
              </FormLabel>
              <Input
                data-testid="businessName"
                placeholder={t('business_details.yourBusinessName')}
                variant="primary"
                value={formData.legalName}
                onChange={(e) => updateData({
                  ...formData,
                  legalName: e.target.value
                })}
              />
            </FormControl>

            <FormControl variant="primary">
              <FormLabel variant="primary">
                {t('business_details.dbaOptional')}
              </FormLabel>
              <Input
                data-testid="dba"
                placeholder={t('business_details.dba')}
                variant="primary"
                value={formData.dba}
                onChange={(e) => updateData({
                  ...formData,
                  dba: e.target.value
                })}
              />
            </FormControl>

            <FormControl variant="primary">
              <FormLabel variant="primary">
                {t('business_details.entityType')}
              </FormLabel>
              <Select
                data-testid="entityType"
                variant="primary"
                value={formData.entityType}
                onChange={(e) => updateData({
                  ...formData,
                  entityType: e.target.value
                })}
              >
                {entityTypes.map((e) => <option key={e.key} value={e.key}>{e.label}</option>)}
              </Select>
            </FormControl>

            <FormControl variant="primary">
              <FormLabel variant="primary">
                {t('business_details.ein')}
              </FormLabel>
              <EinInput
                value={formData.idNumber}
                onChange={(v) => {
                  updateData({
                    ...formData,
                    idNumber: v
                  });
                }}
              />
            </FormControl>

            <Text variant="small" textAlign="left" className="text-light">{t('business_details.noEin')}</Text>
          </Box>
        </MiniBox>

      </form>
    </div>
  );
};

export default BusinessDetails;
